
h1,
h2,
h3,
h4,
h5,
p {
  margin-top: 0;
  margin-bottom: 0;
}


a {
    text-decoration: none;
    color: currentColor;
}

ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

li {
    list-style: none;
}

.list {
    list-style: none;
    padding: 0;
    margin: 0;
}

img {
    display: block;
    max-width: 100%;
    height: auto;
}

svg {
    fill: currentColor;
}

button {
    border: none;
    padding: 0;
    margin: 0;
    background-color: inherit;
    box-shadow: none;
    cursor: pointer;
    font-family: inherit;
}